(function () {
    'use strict';
    var angular = require('angular');
    require('./lead-generation.service');
    require('./lead-generation.component.scss');

    function leadGenController($rootScope, $state, $anchorScroll, leadService, analyticService, UtilService, journeyService) {
        var vm = this, leadReason = "";
        vm.isStaffJourney = journeyService.isStaffJourney();
        $rootScope.showSelectedCard = true;
        $rootScope.showARN = false;

        vm.$onInit = function () {
            $rootScope.stepsHeader.currentStep = 1;
           // vm.annualIncomes = ['3 - 4 lacs', '4 - 5 lacs', '5 - 8 lacs', '8 - 10 lacs', 'above > 10 lacs'];
           vm.annualIncomes = ['4 - 8 lacs', '8 - 10 lacs', 'above > 10 lacs'];
            vm.cities = ['Bengaluru', 'Chennai', 'Gurugram', 'Hyderabad', 'Mumbai', 'New Delhi', 'Noida', 'Pune'];
            vm.selectedCard = journeyService.getSelectedCard();
            initModel();
            $anchorScroll();
            analyticService.trackPageLoad(analyticService.pageList.leadGenerationInput);
        };

        var initModel = function () {
            vm.model = {};
            vm.errorMsg = "";

            var contactDetails = leadService.getContactDetails();
            if (contactDetails) {
                vm.model.givenName = contactDetails.fullName;
                vm.model.mobileNo = contactDetails.mobile;
                vm.model.email = contactDetails.email;
            }
            if ($state.params.leadData) {
                vm.model.givenName = $state.params.leadData.fullName || contactDetails.fullName;
                vm.model.mobileNo = $state.params.leadData.mobileNumber || contactDetails.mobile;
                vm.model.email = $state.params.leadData.email || contactDetails.email;
                vm.model.residenceCity = $state.params.leadData.residenceCity;
                leadReason = $state.params.leadData.leadReason;
                if ($state.params.leadData.statusCode == 1201) {
                    vm.errorMsg = "Sorry! We are unable to progress further. You need to be in the age bracket of 18 to 65 years to apply for an HSBC Credit Card.";
                } else if ($state.params.leadData.statusCode == 1015) {
                    // Maximum number of Aadhaar OTP attempt exceeded
                    if (vm.isStaffJourney) {
                        vm.errorMsg = "Sorry! You've exceeded the maximum number of attempts. Your HSBC representative will inform you of the next steps to complete the application.";
                    } else {
                        vm.errorMsg = "Sorry! You've exceeded the maximum number of attempts. We are unable to progress further. Please provide your contact details. We will contact you within 2 working days to proceed with your Credit Card application.";
                    }
                } else if ($state.params.leadData.statusCode == 1007) {
                    // C11/C35 hit                    
                    if (vm.isStaffJourney) {
                        vm.errorMsg = "Our records indicate that you are an existing customer of HSBC. The HSBC representative will inform you of the next steps to complete the application.";
                    } else {
                        vm.errorMsg = "Our records indicate that you are an existing customer of HSBC. We will contact you within 2 working days to prioritize your Credit Card application.";
                    }
                } else if ($state.params.leadData.statusCode == 1017) {
                    vm.errorMsg = "Aadhaar address is insufficient.";
                } else if ($state.params.leadData.statusCode == 1022) {
                    // 1022: PAN retry count exceeded
                    if (vm.isStaffJourney) {
                        vm.errorMsg = "Sorry! There seems to be some error in the details provided. We are unable to progress further. Your HSBC representative will inform you of the next steps to complete the application.";
                    } else {
                        vm.errorMsg = "Sorry! There seems to be some error in the details provided. We are unable to progress further. Please provide your contact details. We will contact you within 2 working days to proceed with your Credit Card application.";
                    }
                } else if ($state.params.leadData.statusCode == 1023) {
                    // 1023: OTP Re-generate count exceeded the limit
                    if (vm.isStaffJourney) {
                        vm.errorMsg = "Sorry! You've exceeded the maximum number of attempts. Your HSBC representative will inform you of the next steps to complete the application.";
                    } else {
                        vm.errorMsg = "Sorry! You've exceeded the maximum number of attempts. We are unable to progress further. Please provide your contact details. We will contact you within 2 working days to proceed with your Credit Card application.";
                    }
                } else if ($state.params.leadData.statusCode == 1018) {
                    // 1018:  Mobile number is not registered with Aadhaar
                    if (vm.isStaffJourney) {
                        vm.errorMsg = "Sorry! Your mobile number is not registered with Aadhaar. We are unable to progress further. Your HSBC representative will inform you of the next steps to complete the application.";
                    } else {
                        vm.errorMsg = "Sorry! Your mobile number is not registered with Aadhaar. We are unable to progress further. Please provide your contact details. We will contact you shortly to proceed with your Credit Card application.";
                    }
                } else {
                    // 5007: C11/C35 timeout
                    //    2: Failure
                    if (vm.isStaffJourney) {
                        vm.errorMsg = "Sorry! There seems to be a technical problem. Your HSBC representative will inform you of the next steps to complete the application."
                    } else {
                        vm.errorMsg = "Sorry! There seems to be a technical problem. We are unable to progress further. We will contact you within 2 working days to proceed with your Credit Card application."
                    }
                }
            } else {
                // $state.go("app.journey.s1-cards");
                $rootScope.showSelectedCard = false;
            }
        };

        vm.generateLead = function (isValid) {
            if (isValid) {
                var contactDetails = leadService.getContactDetails();
                var leadData = {
                    "annualIncome": vm.model.annualIncome,
                    "residenceCity": vm.model.residenceCity,
                    "email": vm.model.email,
                    "mobile": parseInt(vm.model.mobileNo),
                    "fullName": vm.model.givenName,
                    "leadGenerationReason": leadReason,
                    "leadId": !UtilService.isEmpty(contactDetails) ? contactDetails.leadId : null
                };
                var securityParams = {
                    robocheck: vm.robocheck ? vm.robocheck : ''
                };
                leadService.saveLead(leadData, securityParams).then(function (result) {
                    if (result.success) {
                        $state.go("app.lead-gen-ack", {
                            ackData: {
                                refNumber: result.leadReferenceNumber
                            }
                        });
                    } else {
                        $state.go("app.journey.s1-cards");
                    }
                }, function (error) { });
                analyticService.buttonList.buttonClick.event_content = "Apply now";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            } else {
                vm.model.submitted = true;
                UtilService.focusOnInvalid("leadForm");
            }
        };
    }

    var leadGenComp = angular.module('journey.module').component('leadGeneration', {
        template: require('./lead-generation.component.html'),
        controller: ['$rootScope', '$state', '$anchorScroll', 'leadService', 'analyticService', 'UtilService', 'journeyService', leadGenController]
    })
    module.exports = leadGenComp.name;
})();


