(function () {
    'use strict';

    var step2Service = angular.module('journey.module').service('leadService', ['$rootScope', '$q', 'appState', '$http', 'CONFIG', function ($rootScope, $q, appState, $http, CONFIG) {
        var self = this;

        self.saveLead = function (leadData, params) {
            var apiURL, httpMethod, defer = $q.defer();
            if ($rootScope.integrationsActive) {
                httpMethod = "POST";
                apiURL = CONFIG.apiUrl + '/leadgeneration/save' + '?hash_id=' + Math.random();
                leadData.urlParamId = appState.getUrlParamId();
            } else {
                httpMethod = "GET";
                apiURL = 'resources/data/mock/step2/lead_generation_save.json';
            }
            $http({
                method: httpMethod,
                url: apiURL,
                headers: {
                    'Content-Type': 'application/json'
                },
                data: JSON.stringify(leadData),
                params: params
            }).then(function (response) {
                if (response && response.status === 200 && response.data) {
                    defer.resolve(response.data);
                } else {
                    defer.reject();
                }
            }, function (response) {
                defer.reject();
            });
            return defer.promise;
        };

        self.getContactDetails = appState.getContactDetails;
    }]);

    module.exports = step2Service.name;
})();


